.search-container{
    display: flex;
    justify-content: center;
    margin: 10 px;
}
.search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    background: #a592f2;
    color: #e7e9fe;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255,255,255,0.5);
}
.search-input::placeholder{
    color: #e7e9fe;
}
.search-input:focus{
    outline: none;
}

